<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>用户列表</span>
      </div>
      <div>
        <el-form :inline="true">
          <el-form-item label="用户名称">
            <el-input v-model="queryParams.name" placeholder="用户名称"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUsers()">查询</el-button>
          </el-form-item>
          <el-form-item v-if="addPermission">
            <el-button type="primary" @click="reset();showAddForm = true;">新增</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div style="padding:20px;">
        <el-table :data="tableData" style="margin-bottom:20px;">
          <el-table-column label="头像" width="80">
            <template slot-scope="scope">
              <el-image style="height: 60px" :src="scope.row.avatar" fit="scale-down"></el-image>
            </template>
          </el-table-column>
          <el-table-column prop="username" label="用户名"></el-table-column>
          <el-table-column prop="nickname" label="昵称"></el-table-column>
          <el-table-column prop="roleNames" label="角色"></el-table-column>
          <el-table-column prop="email" label="邮箱"></el-table-column>
          <el-table-column prop="createTime" label="创建时间"></el-table-column>
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-button v-if="editPermission" type="text" @click="edit(scope.row)">编辑</el-button>
              <el-popconfirm
                v-if="delPermission"
                title="确定删除本条数据吗？删除后不可恢复。"
                cancelButtonType="default"
                style="margin-left:10px;"
                @onConfirm="del(scope.row)"
              >
                <el-button type="text" slot="reference">删除</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          :background="false"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryParams.pageNum"
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="queryParams.pageSize"
          :layout="optionsArr.join(',')"
          :total="total"
        ></el-pagination>
      </div>
    </el-card>
    <el-dialog title="编辑标签" width="500px" :visible.sync="showAddForm">
      <el-form :model="editParams" label-position="right" label-width="100px" class="vertical-form">
        <el-form-item label="用户名">
          <el-input v-model="editParams.username" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item label="昵称">
          <el-input v-model="editParams.nickname" placeholder="昵称"></el-input>
        </el-form-item>
        <el-form-item label="角色">
          <el-select v-model="editParams.roleIds" multiple placeholder="请选择角色">
            <el-option v-for="item in roles" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="editParams.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="editParams.description" type="textarea" placeholder="描述"></el-input>
        </el-form-item>
        <el-form-item label="头像">
          <el-upload
            action="/api/admin/upload/image"
            :on-remove="handleRemove"
            :on-success="onSuccess"
            :file-list="fileList"
            :limit="1"
            list-type="picture"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showAddForm = false;reset()">取消</el-button>
        <el-button type="primary" @click="save">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import userApi from "@/api/userApi";
import roleApi from "@/api/roleApi";
import store from "@/store";
export default {
  data() {
    return {
      addPermission: false,
      editPermission: false,
      delPermission: false,
      showAddForm: false,
      optionsArr: ["total", "sizes", "prev", "pager", "next", "jumper"],
      roles: [],
      fileList: [],
      tableData: [],
      groups: [],
      editParams: {},
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0
    };
  },
  created() {
    this.addPermission = store.state.user.permissions.indexOf(600102001) > -1;
    this.editPermission = store.state.user.permissions.indexOf(600102002) > -1;
    this.delPermission = store.state.user.permissions.indexOf(600102003) > -1;
    this.getUsers();
    this.getRoles();
  },
  computed: {
    editTypeChange() {
      return this.editType;
    }
  },
  watch: {},
  methods: {
    getUsers() {
      if (this.queryParams.name == "") {
        this.queryParams.name = null;
      }
      userApi.page(this.queryParams).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    getRoles() {
      roleApi.findAll().then(res => {
        this.roles = res.data;
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getUsers();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getUsers();
    },
    onSuccess(res, file, fileList) {
      file.url = res.data.url;
      this.fileList = fileList;
    },
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    save(formName) {
      if (!this.editParams.username) {
        this.$message.warning("请填写用户名");
        return false;
      }
      if (!this.editParams.nickname) {
        this.$message.warning("请填写昵称");
        return false;
      }
      if (this.editParams.roleIds.length == 0) {
        this.$message.warning("请选择角色");
        return false;
      }
      if (this.fileList.length > 0) {
        this.editParams.avatar = this.fileList[0].url;
      }
      userApi.save(this.editParams).then(res => {
        this.getUsers();
        this.$message.success("保存成功");
        this.reset();
      });
    },
    edit(row) {
      this.showAddForm = true;
      this.editParams = row;
      this.editParams.roleIds = row.roleIdStr.split(",").map(o => parseInt(o));
      if (row.avatar) {
        this.fileList = [{ name: row.avatar.split("@@")[1], url: row.avatar }];
      }
    },
    del(row) {
      userApi.delete(row.id).then(res => {
        this.$message.success("已删除");
        this.getUsers();
      });
    },
    reset() {
      this.showAddForm = false;
      this.editParams = {};
      this.fileList = [];
    }
  }
};
</script>

